import React, {PureComponent} from 'react';
import Head from 'next/head';
import Button from 'antd/lib/button';
import Carousel from 'antd/lib/carousel';
import '../style.css';




export default class Home extends PureComponent {
	renderCarousel = () => {
		return (
				<Carousel autoplay>
				    <div><img src="/static/img/carousel-1.jpeg" /></div>
				    <div><img src="/static/img/carousel-2.jpeg" /></div>
				    <div><img src="/static/img/carousel-3.jpeg" /></div>
				  </Carousel>
			)

	}

	render() {
		return (
			<div>
				<Head>
					<title>home</title>
				    <meta name="viewport" content="initial-scale=1.0, width=device-width" />
				    <link rel='stylesheet' href='https://cdnjs.cloudflare.com/ajax/libs/antd/3.6.3/antd.css'/>
				</Head>
				<div className="framework">
					{this.renderCarousel()}
					<p>港机销售</p>
					<Button type="primary">Button</Button>
				</div>
				
			</div>
		)
	}
}	



